Office.onReady(info => {
  if (info.host === Office.HostType.Outlook) {
    document.getElementById("submit-to-llm").onclick = handleLLMRequest;
    document.getElementById("discard-response").onclick = hideLLMResponse;
    document.getElementById("reply-with-content").onclick = replyWithLLMContent;
  }
});

async function handleLLMRequest() {
  showStatus("Getting email body...");

  Office.context.mailbox.item.body.getAsync("text", async result => {
    if (result.status !== Office.AsyncResultStatus.Succeeded) {
      showStatus("Failed to get email body: " + result.error.message, true);
      return;
    }

    const emailBody = result.value;
    showStatus("Sending to LLM...");

    try {
      // Import from Gradio CDN dynamically
      const { Client } = window.gradio_client;

      // Connect to your locally running or remote Gradio app
      const client = await Client.connect("http://localhost:8082/");
      const response = await client.predict("/email_reply", {
        email_body: emailBody,
        query: "Please reply to this e-mail:"
      });

      const replyText = response.data;
      document.getElementById("llm-response-text").value = replyText;
      document.getElementById("llm-response-area").style.display = "block";
      showStatus("Response received.");
    } catch (err) {
      console.error("Gradio API error:", err);
      showStatus("Error contacting LLM API: " + err.message, true);
    }
  });
}

function hideLLMResponse() {
  document.getElementById("llm-response-area").style.display = "none";
  document.getElementById("llm-response-text").value = "";
  showStatus("Response discarded.");
}

function replyWithLLMContent() {
  const replyText = document.getElementById("llm-response-text").value;
  Office.context.mailbox.item.displayReplyForm(replyText);
}

function showStatus(message, isError = false) {
  const statusEl = document.getElementById("status-message");
  statusEl.textContent = message;
  statusEl.style.color = isError ? "red" : "black";
}
